import { useIdentity } from "../hooks/identity";
import { DropdownItem } from '../components/MultilevelMenu';
import { FaPlus, FaCheck, 
    FaSkull, FaBolt, FaClock } from "react-icons/fa";
import { useRef } from "react";
import { Modal } from "./layout/Modal";
import s from "../style/form.module.sass"
import styled from "../style/style.module.sass";
import { EyeOnMount } from "./layout/EyeOfJudgement";


export function AccountPicker() {
    var [id, axios] = useIdentity();
    var modalRef = useRef();

    function onClick(idx) {
        if (id.active === idx) 
            return id.setActive(null);  
        if (id.users[idx].error) 
            return;

        id.setActive(idx)
    }

    function icon(idx) {
        if (id.users[idx].error) {
            switch(id.users[idx].error.icon) {
                case "bolt":
                    return <FaBolt data-error/>;
                case "time":
                    return <FaClock data-error/>;
                case "death":
                    return <FaSkull data-error/>;
            }
        } else if (idx === id.active) {
            return <FaCheck/>;
        }
    }

    function addAccountClick(evt) {
        modalRef.current.toggleModal();
    }

    return <>
        {id.users.map((user, idx)=> {
            return (
            <DropdownItem 
                text={user.username} 
                rightIcon={icon(idx)} 
                key = {idx}
                onClick={() => onClick(idx)}/>
        );})}
        <DropdownItem leftIcon={<FaPlus/>} text="Add Acount" onClick={addAccountClick}/>

        <Modal ref={modalRef} className={s.accountFormWrap}>
            <EyeOnMount size={150}></EyeOnMount>
            <h1>Login</h1>
            <p>Ok listen i am working on this<br/> Give me a break</p>
            <p style={{fontSize: "2em"}}>Did you see how cool the eye moved? <span className={styled.fabulous}>Fabulous!</span></p>
        </Modal>
    </>;
    
}
